.dashboard {
    display: grid;
    grid-template-columns: minmax(3rem, 1fr) repeat(6, minmax(6rem, 12rem)) minmax(3rem, 1fr);
}
.dash_content {
    grid-column: 2/8;
    padding: 2rem 0;
    position: relative;
}
.dash_content .pic {
    grid-column: 1/2;
    background-color: var(--color-primary);
    text-align: center;
}
.pic img {
    width: 20%;
    padding: 1rem;
}
.dash_content .name {
    color: var(--color-primary);
    grid-column: 2/-1;
    font-size: 3rem;
    font-weight: 600;
    margin: 2rem 0;
}
.dash_content p {
    font-size: 1.5rem;
    padding: .5rem;
}
.earnings {
    display: flex;
    justify-content: space-between;
    padding: 2rem;
}
.earnings .inv, .earnings .earn {
    border: 1px solid currentColor;
    padding: 2rem 4rem;
    margin: 1rem;
}
.earnings .inv p, .earnings .earn p {
    font-size: 3rem;
    padding: 1rem 0;
    text-align: right;
}
.earnings .inv {
    color: darkorange;
}
.earnings .earn {
    color: mediumseagreen;
}
.dash_content button {
    padding: 1rem 2rem;
    font-size: 1.2rem;
    color: var(--white);
    background-color: var(--color-primary);
    border: none;
    transition: all .4s;
}
.dash_content button:hover {
    background-color: var(--primary-dark);
    box-shadow: 1px 1px 5px black;
}
.dash_content .logout {
    position: absolute;
    top: 16rem;
    right: .5rem;
    font-size: 1rem;
    padding: 1rem;
}


/* ///////////////////// styling popup /////////////////////// */
.popup {
    padding: 4rem 3rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 5px 5px 1rem black;
    background-color: rgba(0, 0, 0, 0.932);
    text-align: center;
    border-radius: 1rem;
    color: var(--white);
}

.popup .bitlogo {
    width: 10rem;
    margin: 2rem 0;
}
.popup label {
    padding: .5rem 1rem;
    cursor: pointer;
    background-color: rgb(252, 122, 0);
}
.popup .waiting {
    background-color: darkgoldenrod;
    padding: .4rem 1rem;
    border-radius: .5rem;
    color: var(--white);
    width: max-content;
    margin: 1rem auto;
    display: flex;
    align-items: center;

}
.popup .waiting img {
    width: 1.5rem;
    margin-right: 2rem;
}
.popup p {
    font-size: 1.2rem;
    padding: .5rem;
    margin: 2rem auto;
    text-align: left;
    color: green;
    border: .5px solid rgb(141, 141, 141);
    border-radius: 1rem;
    width: max-content;
    overflow: scroll;
}
.popup button {
    padding: 1rem 2rem;
    background-color: var(--color-primary);
    color: var(--white);
    border: none;
    margin-top: 2rem;
}



/* ///////////////////// styling payments history section ////////////////////////////// */

.dashboard .payment_hostory {
    margin-top: 5rem;
}
.dashboard .payment_hostory .head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 900;
    font-size: 1.5rem;
    padding: 1rem;
    color: rgb(100, 100, 100);
    background-color: rgb(209, 209, 209);
    margin-bottom: 2rem;
    cursor: pointer;
}
.payment_hostory .head span {
    text-shadow: -1px 1px 1px black;
    transition: all .5s;
}
.rotate {
    transform: rotate(180deg);
}
.dashboard .payment {
    padding: .5rem 1rem;
    display: flex;
    justify-content: space-between;
    border: 1px solid rgb(209, 209, 209);
    align-items: center;
}
.payment button {
    padding: .5rem;
    font-size: .8rem;
}
.payment .recieved_btn p {
    background-color: blue;
    padding: .3rem .5rem;
    font-size: .8rem;
    color: white;
    opacity: .5;
}

@media only screen and (max-width: 600px) {
    .dashboard {
        display: block;
    }
    .dash_content {
        padding: 0;
        padding-bottom: 5rem;
    }
    .dash_content .name {
        font-size: 2rem;
    }
    .earnings {
        display: block;
        padding: 0;
    }
    .earnings .inv , .earnings .earn {
        padding: 2rem 1rem;
    }
    .popup {
        padding: 2rem;
        width: 70vw;
    }
    .popup h1 {
        font-size: 1.2rem;
        text-align: left;
        padding: 0;
    }
    .popup p {
        font-size: 1.2rem;
        width: 17rem;
    }
}